<template>
  <div class="special-drink-parts">
    <!-- 标题 -->
    <p class="sd-title">{{drinkList.moduleTitle || ''}}</p>
    <!-- 主体 -->
    <div class="sd-main">
      <van-image
        class="order-img"
        height="3.6rem"
        width="100%"
        fit="cover"
        lazy-load
        @click="handleTopLink(drinkList.topBanner)"
        :src="drinkList.topBanner.pic | imageToHttps | imageToWebp($webpSurpport)"
      />
      <van-swipe ref='vanSwiper' :loop="false" indicator-color="white" class="banner">
        <van-swipe-item v-for="(item, index) in drinkList.bannerList || []" :key="index" class="banner-items">
          <div class="l-b">
            <van-image
              height="4.32rem"
              width="100%"
              fit="cover"
              :src="item[0].pic | imageToHttps | imageThumb('500') | imageToWebp($webpSurpport)"
              @click="bannerClick(item[0])"/>
          </div>
          <div class="r-b">
            <van-image
              height="2.16rem"
              width="100%"
              fit="cover"
              :src="item[1].pic | imageToHttps | imageThumb('500') | imageToWebp($webpSurpport)"
              @click="bannerClick(item[1])"/>
            <van-image
              height="2.16rem"
              width="100%"
              fit="cover"
              :src="item[2].pic | imageToHttps | imageThumb('500') | imageToWebp($webpSurpport)"
              @click="bannerClick(item[2])"/>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>
<script>
// import Utils from '@/utils/utils'
import commons from '@/utils/commons.js'

export default {
  props: {
    drinkList: {
      type: Object,
      default: () => { return {} }
    }
  },
  data() {
    return {
    }
  },
  mounted() { commons.swipeResize(this, 'vanSwiper') },
  methods: {
    /**
     * @ description 点击轮播图跳转
     */
    bannerClick(item) {
      const { linkType, link, relationId } = item
      this.$store.dispatch('bannerClick', { hyperLinkType: String(linkType), hyperLink: link, relationId })
      // let id = item.link.split('productId=')[1]
      // this.$router.push({
      //   path: 'goodsDetail',
      //   query: {
      //     productId: id
      //   }
      // })
    },
    /**
     * @ description 顶部图片点击跳转
     */
    handleTopLink(item) {
      const { linkType, link, relationId } = item
      // Utils.bannerClick(String(linkType), link, relationId)
      this.$store.dispatch('bannerClick', { hyperLinkType: String(linkType), hyperLink: link, relationId })
    }
  }
}
</script>
