  <div class="bottom-list-parts">
    <!-- 品类切换 -->
    <div :class='{"tab-area":true, "tab-fixed":tabFixed}'>
      <div v-for="(item, index) in typeList" :key="index" class="tab-items">
        <input type="radio" class="tab-radio" name="tab" :id="index" @change="hadleTypeChange(item)" :checked="index === 0" />
        <label :for="index" class="tab-btn">{{item.name}}</label>
      </div>
    </div>
    <div v-if="tabFixed" class="fixed-divider"></div>
    <!-- 商品列表 -->
    <van-list v-model="loading" @load="getProductList" :finished="finished" finished-text="没有更多啦~">
      <public-product-list :productList="goodsList" :switchStyle="true"/>
