<script>
import { getISGoodsTypeInfo } from '@/http/service/specialtyStore'
const publicProductList = () => import(/* webpackChunkName: "productList" */'@/components/publicProductList/index')
export default {
  props: {
    typeList: { // 分类数据
      type: Array,
      default: () => { return [] }
    },
    tabFixed: { // tab是否吸顶
      type: Boolean,
      default: () => { return false }
    }
  },
  components: {
    publicProductList
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    }
  },
  watch: {
    typeList: {
      handler(newVal, oldVal) {
        this.hadleTypeChange(newVal[0])
      },
      immediate: true
    }
  },
  data() {
    return {
      loading: false,
      finished: true,
      pageIndex: 1,
      goodsList: [] // 商品列表
    }
  },
  methods: {
    /**
     * @description 切换品类
     * @param {number} cId 城市id
     * @param {number} cateId 分类编号
     * @param {number} pId 省id
     * @param {number} rId 地区编号
     * @param {number} sId 街道编号
     * @param {number} type 类型
     */
    hadleTypeChange(item) {
      getISGoodsTypeInfo({
        cId: this.location.cityId,
        cateId: item.cId,
        pId: this.location.provinceId,
        rId: this.location.districtId,
        sId: this.location.townshipId,
        type: 3,
      }).then(res => {
        if (res.success) {
          let list = []

          !!res.data && res.data.map(item => {
            list.push({
              ...item,
              productId: item.pId,
              defaultImageUrl: item.pic,
              sku: item.sku,
              productFullName: item.name,
              finalPrice: item.price,
            })
          });
          this.goodsList = list;
        } else {
          this.$toast(res.message)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    /**
     * @description 进入详情
     */
    goDetail(item) {
      this.$router.push(`/goodsDetail?productId=${item.pId}`)
      // if (this.$util.isSgRn()) {
      //   window.postMessage(
      //     JSON.stringify({
      //       type: 'goProduct',
      //       data: {
      //         id: item.pId
      //       }
      //     })
      //   )
      // } else {
      //   this.$router.push(`/goodsDetail?productId=${item.pId}`)
      // }
    },
    /**
     * @description 上拉加载 (暂时不加此功能)
     */
    getProductList() {
      // this.loading = true
      // this.finished = false
      // this.pageIndex++
    },
  }
}
</script>
