<template>
  <div class="special-snacks-parts">
    <!-- 标题 -->
    <p class="ss-title">{{foodList.moduleTitle || ''}}</p>
    <!-- 主体 -->
    <div class="ss-main">
      <van-image
        class="order-img"
        height="3.6rem"
        width="100%"
        fit="cover"
        lazy-load
        @click="handleTopLink(foodList.topBanner.link)"
        :src="foodList.topBanner && foodList.topBanner.pic || '' | imageToHttps | imageToWebp($webpSurpport)"
      />
      <!-- 商品列表 -->
      <div class="ss-goods-list">
        <div v-for="(item, index) in foodList.productList" :key="index" class="list-items" @click="goDetail(item)">
          <van-image
            height="2rem"
            width="2rem"
            fit="cover"
            lazy-load
            :src="item.imageUrl"
          />
          <div class="goods-name">{{item.name}}</div>
          <div class="prod-price">
            <span class="symbol">￥</span>{{item.price | formatPrice}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    foodList: {
      type: Object,
      default: () => { return {} }
    }
  },
  data() {
    return {

    }
  },
  methods: {
    /**
     * @ description 顶部图片点击跳转
     */
    handleTopLink(link) {
      let storeId = link.split('storeId=')[1]
      this.$router.push({
        path: '/shopDetail',
        query: {
          storeId,
        }
      })
    },
    /**
     * @description 进入详情
     */
    goDetail(item) {
      this.$router.push(`/goodsDetail?productId=${item.productId}`)
      // if (this.$util.isSgRn()) {
      //   window.postMessage(
      //     JSON.stringify({
      //       type: 'goProduct',
      //       data: {
      //         id: item.id
      //       }
      //     })
      //   )
      // } else {
      //   this.$router.push(`/goodsDetail?productId=${item.id}`)
      // }
    }
  }
}
</script>
